/* font.css : 자주 사용되어지는 텍스트를 이곳에서 꾸며보기 (컴포넌트 방식)  */


/* Google Fonts 웹 폰트 : 노토산스*/
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');

/* 
100 → Thin
300 → Light
400 → Regular
500 → Medium
600 → SemiBold
700 → Bold
900 → Black
*/

.bt01 {
  font-size: 15px;
  width: 100px;
  height: 33px;
  display: block;
  line-height: 33px;
  /*수직으로 가운데(한줄 방식) */
  border: 1px solid #ccc;
  border-radius: 20px;
  color: #999;
  float: left;
}

/*  header 시작 */

/* pc형 */

/* header 시작 */

header {
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
  height: 100px;
  z-index: 50;
  transition: transform 0.4s ease-in-out;
}

header div.outer {
  width: 93.75%;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

/* 태블릿 로고 (안보임 처리) */
header div.outer div.tm-logo {
  display: none;
}

header div.outer nav.main-nav {
  width: 100%;
  height: 100%;
  background-color: #fff;
  display: flex;
  /* Flexbox 적용 */
  justify-content: space-between;
  /* 양쪽 정렬 */
  align-items: center;
  /* 세로 중앙 정렬 */
  padding: 10px 0;
  width: 100%;
  margin: 0 auto;
  /* box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1); */
}

header div.outer nav.main-nav h1.pc-logo {
  display: flex;
  /* Flexbox 적용 */
  align-items: center;
  /* 로고 텍스트 & 이미지 정렬 */
  margin-right: 20px;
  /* gnb와 간격 */
}


header div.outer nav.main-nav ul.gnb {
  display: flex;
  justify-content: center;
  /* 중앙 정렬 */
  align-items: center;
  /* 세로 중앙 */
  flex-grow: 1;
  /* 남는 공간 차지해서 중앙 정렬 유지 */
  list-style: none;
  margin: 0;
  padding: 0;
  height: 100%;

}

header div.outer nav.main-nav ul.gnb li {
  margin-right: 20px;
  /* 메뉴 간격 */
  position: relative;
  text-align: center;
}

header div.outer nav.main-nav ul.gnb li a {
  display: block;
  padding: 20px 30px;
  color: #333;
  font-weight: 500;
  font-size: 22px;
  text-decoration: none;
  transition: all 0.3s;
}


/* 서브메뉴 스타일 */
header div.outer nav.main-nav ul.gnb li ul.sub-menu {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  min-width: 200px;
  background-color: rgba(255, 255, 255, 0.95);
  padding: 15px 0;
  border-radius: 8px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 0, 0, 0.1);
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  backdrop-filter: blur(8px);
  /* text-align:left; */
}

header div.outer nav.main-nav ul.gnb li:hover .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: translateX(-50%) translateY(5px);
}

header div.outer nav.main-nav ul.gnb li .sub-menu li {
  padding: 0 10px;
  margin: 5px 0;
}

header div.outer nav.main-nav ul.gnb li .sub-menu a {
  display: block;
  padding: 12px 20px;
  color: #333;
  font-size: 16px;
  border-radius: 6px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  text-align: left;
}

header div.outer nav.main-nav ul.gnb li .sub-menu a:before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #ffcc33 0%, #ffb400 40%, #ff8400 100%);
  transition: width 0.3s ease;
}

header div.outer nav.main-nav ul.gnb li .sub-menu a:hover {
  color: #ff0000;
  background-color: rgba(255, 0, 0, 0.05);
  transform: translateX(5px);
}

header div.outer nav.main-nav ul.gnb li .sub-menu a:hover:before {
  width: 100%;
}


/* right 영역 */

/* RightMenu 절대 위치 설정 */
header div.outer nav.main-nav #RightMenu {
  display: flex;
  align-items: center;
}

/* block으로 내부 요소 명확히 수평 정렬 */
header div.outer nav.main-nav #RightMenu div.block {
  display: flex;
  align-items: center;
}

/* 로그인, 장바구니 메뉴 수평정렬 */
header div.outer nav.main-nav #RightMenu div.block ul.infoMenu {
  display: inline-block;
  vertical-align: middle;
}

header div.outer nav.main-nav #RightMenu div.block ul.infoMenu li {
  display: inline-block;
  width: auto;
  margin-right: 20px;
  /* 메뉴 간 간격 */
}

header div.outer nav.main-nav #RightMenu div.block ul.infoMenu li a span {
  color: #888;
  font-family: "Noto Sans KR", sans-serif;
}

/* 검색폼 수평정렬 */
header div.outer nav.main-nav #RightMenu div.block form {
  position: relative;
}

header div.outer nav.main-nav #RightMenu div.block form input[type="text"] {
  width: 180px;
  height: 33px;
  padding-left: 10px;
  border-radius: 20px;
  border: 1px solid #999;
  font-size: 12px;
}

header div.outer nav.main-nav #RightMenu div.block input[type="image"] {
  width: 20px;
  position: absolute;
  right: 10px;
  top: 6px;
}



/* header 종료 */
/* tablet형 시작 */

@media (min-width: 767px) and (max-width: 1279px) {
  header {
