  header div.outer nav.main-nav ul.gnb li a {
    font-size: 20px;
  }

  header div.outer nav.main-nav ul.gnb li .sub-menu a {
    font-size: 14px;
  }

  /* tablet 로고 */

  /* pc logo 없앰처리 */
  #nav h1.pc-logo {
    display: none;
  }

  /* tablet 로고,메뉴 */
  header div.outer div.tm-logo {
    display: block;
  }

  header div.outer div.tm-logo h1.tm-logo {
    /* border:1px solid red; */
    display: inline-block;
    position: absolute;
    top: 25%;
    left: 0;
  }

  header div.outer div.tm-logo h1.tm-logo a {
    display: inline-block;
  }

  header div.outer div.tm-logo h1.tm-logo a img {
    width: 35px;
    height: 35px;
  }

  header div.outer div.tm-logo h1.tm-menu {
    /* border:1px solid red; */
    display: inline-block;
    position: absolute;
    top: 25%;
    right: 0;
  }

  header div.outer div.tm-logo h1.tm-menu a {
    display: inline-block;
  }

  header div.outer div.tm-logo h1.tm-menu a img {
    width: 35px;
    height: 35px;
  }

  /* tablet right 메뉴 없앰 */
  header div.outer nav.main-nav #RightMenu {
    display: none;
  }
}

/* mobile PC */

/* 900px 이하 미디어 쿼리 */
@media (max-width: 766px) {

  /* ✅ header를 column-reverse로 변경 → nav를 위로 올림 */
  header {
    position: relative;
    width: 100%;
    height: auto;
    background: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 50;
  }

  header div.outer {
    /* border:3px solid gold; */
  }

  /* ✅ nav를 가로 100%로 확장 */
  header div.outer nav.main-nav {
    width: 100%;
    max-width: 1200px;
    /* ✅ 최대 너비 제한 */
    height: 80px;
    display: flex;
    justify-content: center;
    /* ✅ 내부 요소 중앙 정렬 */
    align-items: center;
    background: white;
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
    border-top: 1px solid rgba(204, 204, 204, 0.6)
      /* ✅ 위아래 균형 맞추기 */
  }

  /* ✅ 메뉴 리스트를 100%로 확장하고 중앙 정렬 */
  header div.outer nav.main-nav ul.gnb {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
  }

  header div.outer nav.main-nav ul.gnb li {
    margin: 0 auto;

  }

  header div.outer nav.main-nav ul.gnb li a {
    display: block;
    padding: 15px 20px;
    text-decoration: none;
    font-size: 25px;
    font-weight: 500;
    color: black;
    /* border:3px solid red; */
  }

  header div.outer nav.main-nav ul.gnb li a.main-mobile-left1 {
    padding-left: 0;
  }

  header div.outer nav.main-nav ul.gnb li:last-child a {
    padding-right: 0;
  }

  /* 서브메뉴 값 제어 */

  header div.outer nav.main-nav ul.gnb li .sub-menu {
    left: 50%;
    transform: translateX(-50%);
  }

  /* ✅ 첫 번째 메뉴의 서브 메뉴는 왼쪽 정렬 */
  header div.outer nav.main-nav ul.gnb li:first-child .sub-menu {
    left: 0;
    transform: none;
  }

  /* ✅ 마지막 메뉴의 서브 메뉴는 오른쪽 정렬 */
  header div.outer nav.main-nav ul.gnb li:last-child .sub-menu {
    right: 0;
    left: auto;
    transform: none;
  }

  /* gl.unb 1,5번째 좌,우측 값 0으로 바꾸기. */
  header div.outer nav.main-nav ul.gnb li:first-child {
    padding-left: 0;
    margin-left: 0;
  }

  header div.outer nav.main-nav ul.gnb li:last-child {
    padding-right: 0;
    margin-right: 0;
  }

  /* ✅ 1열: tm-logo + tm-menu */
  header div.outer div.tm-logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 80px;
    position: relative;
  }

  /* ✅ tm-logo 왼쪽 정렬 */
  header div.outer div.tm-logo h1.tm-logo {
    display: inline-block;
    position: relative;
  }

  /* ✅ tm-menu 오른쪽 정렬 */
  header div.outer div.tm-logo h1.tm-menu {
    display: inline-block;
    position: relative;
  }

  /*  PC 로고 숨기기 */
  #nav h1.pc-logo {
    display: none;
  }

  /* tablet에서 RightMenu 숨김 */
  header div.outer nav.main-nav #RightMenu {
    display: none;
  }

  /*  모바일용 로고 이미지 크기 조정 */
  header div.outer div.tm-logo h1.tm-logo a img,
  header div.outer div.tm-logo h1.tm-menu a img {
    width: 35px;
    height: 35px;
  }

}

@media all and (max-width:500px) and (min-width:320px) {
  header {
    position: relative;
    width: 100%;
    height: auto;
    background: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 50;
  }

  header div.outer {
    width: 88%;
  }

  /* ✅ 네비게이션 정렬 */
  header div.outer nav.main-nav {
    width: 100%;
    max-width: 1200px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: white;
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
  }

  header div.outer nav.main-nav ul.gnb {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
  }

  header div.outer nav.main-nav ul.gnb li {
    margin: 0 auto;
  }

  header div.outer nav.main-nav ul.gnb li a {
    display: block;
    padding: clamp(10px, 3vw, 15px) clamp(15px, 4vw, 20px);
    text-decoration: none;
    font-size: clamp(22px, 3.5vw, 24px);
    font-weight: 500;
    color: black;
    /* border:3px solid red */
  }

  /* ✅ 서브메뉴 정렬 */
  header div.outer nav.main-nav ul.gnb li .sub-menu {
    left: 50%;
    transform: translateX(-50%);
    min-width: 40vw;
    padding: 3vw 0;
  }

}

