/* pc형 */
/* footer 시작 */

footer {
  /* border:3px solid red; */
  width: 100%;
  height: auto;
  background: rgba(204, 204, 204, 0.3);
  padding-top: 50px;
  position: relative;
}

footer div.outer {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  /* border:3px solid blue; */
}

footer div.outer div.inner {
  /* border:3px solid green; */
  width: auto;
  margin: 0 auto;

}

footer div.outer div.inner ul.f-menu {
  /* border:3px solid pink; */
  text-align: center;
}

footer div.outer div.inner ul.f-menu li {
  display: inline-block;
  font-weight: 400;
  font-style: normal;
  font-size: 20px;
  color: rgba(204, 204, 204, 0.3);
  margin-right: 2.5vw;
  /* ✅ 구분선과 글자 사이 여백 */
  text-align: center;
  position: relative;
  padding-right: 2vw;
  /* ✅ 구분선 위치 안정화 */
}

footer div.outer div.inner ul.f-menu li::after {
  content: " | ";
  color: #999;
  position: absolute;
  right: -1vw;
  /* ✅ 구분선과 글자 사이 여백 */
  top: 50%;
  transform: translateY(-50%);
  /* ✅ 수직 중앙 정렬 */
}

/* ✅ 마지막 요소에는 `|` 제거 */
footer div.outer div.inner ul.f-menu li:last-child::after {
  content: "";
}


footer div.outer div.inner ul.f-menu li a {
  /* border:3px solid orange; */
  display: inline-block;
  /* padding-right:3vw; */
  font-size: 20px;
  line-height: 2.5;
  color: #333;
}

footer div.outer div.inner ul.f-menu li a:hover {}

footer div.outer div.inner ul.f-menu li:nth-child(3) {
  font-weight: 600;
  color: #000;
  /* border:3px solid red; */
}

footer div.outer div.inner ul.f-button {
  text-align: center;
  padding-top: 3vh;
  padding-bottom: 3vh;
}

footer div.outer div.inner ul.f-button li {
  display: inline-block;
}

footer div.outer div.inner ul.f-button li a {
  box-sizing: border-box;
  appearance: none;
  background-color: transparent;
  border: 2px solid black;
  /* 기존 $red */
  border-radius: 0.6em;
  color: black;
  /* 기존 $red */
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  padding: 1.2em 2.8em;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
}

footer div.outer div.inner ul.f-button li a:hover {
  box-shadow: 0 0 40px 40px black inset;
  color: #fff;
  outline: 0;
}

footer div.outer div.inner div.info {
  /* border:3px solid orangered; */
  text-align: center;
}

footer div.outer div.inner div.info p {
  font-weight: 400;
  /* Regular */
  font-style: normal;
  font-size: 20px;
  color: #666;
  display: block;
  line-height: 2.5;
  /* border:3px solid maroon; */
  white-space: nowrap;
  /* 줄바꿈 방지 */
  overflow: hidden;
  /* 넘치는 텍스트 숨김 */
  text-overflow: ellipsis;
}



footer div.outer div.inner div.sns {
  /* border:3px solid skyblue; */
  padding-top: 3vh;
  padding-bottom: 5vh;
}

footer div.outer div.inner div.sns figure {
  width: 100%;
  height: auto;
  text-align: center;
  /* ✅ 가운데 정렬 */

}

footer div.outer div.inner div.sns figure p {
  background-size: contain;
  display: inline-block;
  width: 100%;
  height: auto;
  max-width: 60px;
  min-width: 30px;
  aspect-ratio: 1 / 1;
  margin-right: 2vw;
}

/* ✅ 각 SNS 아이콘 별 이미지 설정 */
footer div.outer div.inner div.sns figure p.fa {
  background-image: url(../assets/images/common/facebook.gif);
}

footer div.outer div.inner div.sns figure p.in {
  background-image: url(../assets/images/common/instargram.gif);
}

footer div.outer div.inner div.sns figure p.yo {
  background-image: url(../assets/images/common/youtube.gif);
  margin-right: 0;
}


