/* Main pc형 */
/* index 시작 */
#Main-Visual {
  width: 100%;
  max-height: 700px;
  height:100%;
  /* border: 3px solid red; */
}

#Main-Visual div.inner {
  width: 93.75%;
  height: auto;
  margin: 0 auto;
  border-radius: 20px;
  background: linear-gradient(135deg, #ffcc33 0%, #ffb400 40%, #ff8400 100%);
  overflow:hidden;
  display: flex; /* flex 적용 */
  align-items: center; /* 세로 중앙 정렬 */
  /* border:3px solid blue; */
}

/* 좌측 텍스트 50% */
#Main-Visual div.inner div.left {
  width: 50%;
  text-align:center;
  height:100%;
  /* border:3px solid green; */
}

#Main-Visual div.inner div.left h1 {
  font-size: 65px;
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 700;
  color: #fff;
  margin-bottom: 15px; /* 간격 조정 */
  text-align:center;
  /* border:3px solid red; */
}

#Main-Visual div.inner div.left p {
  font-size: 22px;
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 700;
  color: #fff;
  text-align:center;
  /* border:3px solid blue; */
}

/* 우측 swiper-container 50% */
#Main-Visual div.inner div.right {
  width: 50%;
  height:100%;
  float:right;
}

/* Swiper 구조 최적화 */
#Main-Visual div.inner div.right div.swiper-container {
  width: 100%;
  height: 100%;
}

#Main-Visual div.inner div.right div.swiper-container div.swiper-wrapper {
  width: 100%;
  height: 100%;
}

#Main-Visual div.inner div.right div.swiper-container div.swiper-wrapper div.swiper-slide {
  width: 100%;
  height: 100%;

}

#Main-Visual div.inner div.right div.swiper-container div.swiper-wrapper div.swiper-slide img {
  width: 100%;
  height: auto;
  object-fit: cover;
  /* border:3px solid gold; */
}

.swiper-button-next,
.swiper-button-prev {
  color: #000;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}


/* ========= visual 종료 ========== */
/* ========= c01 시작 ========== */
#c01 {
  
  width: 100%;
