}

#c01 div.outer div.inner {
  width: 86.7%;
  height: 100%;
  margin: 0 auto;
  overflow: hidden;
}

/* ✅ 탭 메뉴 수정 */
#c01 div.outer div.inner .tab-menu {
  text-align: left;
  border-bottom: 1px solid #ccc;

}

#c01 div.outer div.inner .tab-menu .tab-link {
  background: #fff;
  border: none;
  padding: 10px 15px 30px 30px;
  cursor: pointer;
  margin-right: 5px;
  font-weight: 500;
  font-style: normal;
  font-size: 20px;
  color: #999;
  background: url(../assets/images/common/one.gif) no-repeat 0 18px;
  /* border:3px solid red; */

}

#c01 div.outer div.inner .tab-menu .tab-link.active {
  background: #fff;
  color: #000;
}

/* ✅ Swiper4에 맞게 변경 */
#c01 div.outer div.inner .swiper-container4 {
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 0;

}

#c01 div.outer div.inner .swiper-container4 div.swiper-wrapper {
  display: flex;
  transition-property: transform;
}

#c01 div.outer div.inner .swiper-container4 div.swiper-wrapper .swiper-slide {
  /* border:3px solid skyblue; */
}




#c01 div.outer div.inner .swiper-container4 div.swiper-wrapper .swiper-slide .main-product-grid {
  /* border:3px solid gold; */
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  height: auto;
  position: relative;
}

#c01 div.outer div.inner .swiper-container4 div.swiper-wrapper .swiper-slide .main-product-grid .main-product-card {

  background: white;
  text-align: center;
  /* border:3px solid skyblue; */
  height: auto;
  position: relative;

}

/* 🔥 마지막 요소를 제외한 모든 요소에 오른쪽 선 추가 */
#c01 div.outer div.inner .swiper-container4 div.swiper-wrapper .swiper-slide .main-product-grid .main-product-card:not(:nth-child(3n))::after {
  content: "";
  position: absolute;
  top: -45px;
  /* 🔥 선이 위쪽으로 너무 붙지 않게 조정 */
  right: 0;
  width: 1px;
  height: calc(100% - 20px);
  /* 🔥 위아래 여백 추가 */
  background-color: #ccc;

}

#c01 div.outer div.inner .swiper-container4 div.swiper-wrapper .swiper-slide .main-product-grid .main-product-card .main-product-image {
  padding-top: 20px;
  padding-bottom: 20px;
  max-width: 80%;
  height: auto;
  object-fit: cover;
  padding: 30px;

}

#c01 div.outer div.inner .swiper-container4 div.swiper-wrapper .swiper-slide .main-product-grid .main-product-card .main-product-title {
  font-weight: 500;
  font-style: normal;
  font-size: 17px;
  color: #555;
  margin-bottom: 36px;
}

#c01 div.outer div.inner .swiper-container4 div.swiper-wrapper .swiper-slide .main-product-grid .main-product-card .main-product-sub {
  font-weight: 500;
  font-style: normal;
  font-size: 15px;
  color: #999;
  max-width: 280px;
  margin: 0 auto;
  padding-bottom: 40px;
}

#c01 div.outer div.inner .swiper-container4 div.swiper-wrapper .swiper-slide .main-product-grid .main-product-card .main-product-tage {
  overflow: hidden;
  text-align: center;
  display: inline-block;
  pointer-events: none;

}

#c01 div.outer div.inner .swiper-container4 div.swiper-wrapper .swiper-slide .main-product-grid .main-product-card .main-product-tage a {
  font-size: 16px;
  font-weight: 500;
  width: 120px;
  height: 50px;
  display: block;
  line-height: 50px;
  border: 1px solid #ccc;
  border-radius: 20px;
  color: #999;
  float: left;
  text-decoration: none;
  margin-right: 20px;
}

/* ✅ Swiper4 네비게이션 버튼 수정 */
.swiper-button-next4,
.swiper-button-prev4 {
  color: rgba(255, 255, 255, 0);
  display: none;
  padding: 30px;
  border-radius: 50%;
}

.swiper-button-next4:hover,
.swiper-button-prev4:hover {
  background-color: rgba(255, 255, 255, 0);

}

.swiper-pagination4 {
  position: relative;
}

.swiper-pagination4-bullet {
  width: 10px;
  height: 10px;
  background-color: #ccc;
  opacity: 0.5;
}



