/* ===============Brand pc형 시작 =============== */

/* Brand - PC형 시작 */

/* 유튜브 배경 영상 */
#brand-visual {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
  max-height: 700px;
  /* 유지 */
}

#youtube-container {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 56.25vw;
  /* 16:9 비율 유지 (9 / 16 * 100) */
  max-height: 100vh;
  max-width: 177.78vh;
  /* (16 / 9 * 100) */
  transform: translate(-50%, -50%);
  opacity: 0;
  /* 처음엔 숨김 */
  transition: opacity 0.5s ease-in-out;
}

#brand-visual iframe {
  width: 100%;
  height: 100%;
  position: absolute;
  pointer-events: none;
}

#brand-visual .inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  font-size: 48px;
  font-weight: bold;
  font-family: Arial, sans-serif;
  z-index: 10;
  /* background: rgba(0, 0, 0, 0.3); */
  padding: 20px 40px;
  border-radius: 10px;
}

/* 컨트롤 버튼 스타일 */
#controls {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
  z-index: 100;
}

#controls a {
  text-decoration: none;
  color: white;
  background: rgba(0, 0, 0, 0.5);
  padding: 10px 15px;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate(-50%, -60%);
    /* 시작점을 조금 더 위로 조정 */
  }

  to {
    opacity: 1;
    transform: translate(-50%, -50%);
    /* 원래 중앙 정렬된 위치로 복귀 */
  }
}

