  /* border:3px solid gold; */
  display: flex;
  /* flex로 가로 정렬 */
  justify-content: space-between;
  /* 균등 배치 */
  align-items: center;
  /* 수직 중앙 정렬 */
  width: 100%;
}

#content1 div.outer div.inner div.content8 div.div_figure div.box-container .figure {
  flex: 1;
  /* 모든 박스 동일한 크기 */
  position: relative;
  margin: 0 1%;
  /* 박스 간격 */
  overflow: hidden;
  border-radius: 30px;
}

/* 박스 컨테이너 */
div.box-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

/* 개별 figure 스타일 */
div.box-container figure {
  flex: 1;
  position: relative;
  overflow: hidden;
  border-radius: 30px;
  transition: transform 0.3s ease-in-out;
}

/* 기본 이미지 스타일 */
div.box-container figure img {
  width: 100%;
  height: auto;
  display: block;
  transition: opacity 0.35s ease-in-out, transform 0.35s ease-in-out;
  transform: translate3d(0, 0, 0);
}

/* 기본 상태에서 완전히 숨겨진 figcaption (Layla 효과) */
div.box-container figure figcaption {
  position: absolute;
  top: 0;
  /* 박스 크기랑 맞추기 */
  left: 0;
  width: 100%;
  height: 100%;
  /* 오버레이 크기를 figure와 동일하게 설정 */
  background: rgba(0, 0, 0, 0.1);
  /* 기본 상태에서 투명 */
  color: white;
  padding: 20px;
  text-align: center;
  opacity: 0;
  /* 기본적으로 숨김 */
  transition: opacity 0.5s ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* 테두리 애니메이션 */
div.box-container figure figcaption::before,
div.box-container figure figcaption::after {
  content: '';
  position: absolute;
  opacity: 0;
  transition: opacity 0.35s ease-in-out, transform 0.35s ease-in-out;
}

.box-container figure figcaption::before {
  top: 20px;
  right: 20px;
  bottom: 20px;
  left: 20px;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  transform: scale(0, 1);
  transform-origin: 0 0;

}



.box-container figure figcaption::after {
  top: 10px;
  right: 10px;
  bottom: 10px;
  left: 10px;
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
  transform: scale(1, 0);
  transform-origin: 100% 0;
}

/* hover 시 효과 (Layla) */
div.box-container figure:hover img {
  opacity: 0.8;
  transform: translate3d(0, 0, 0);
}

div.box-container figure:hover figcaption {
  opacity: 1;
  /* 텍스트 및 박스 서서히 등장 */
  transform: none;
}

div.box-container figure:hover figcaption::before,
div.box-container figure:hover figcaption::after {
  opacity: 0.5;
  transform: scale(1);
}

/* 텍스트 기본 스타일 */
div.box-container figure h3,
div.box-container figure p {
  opacity: 0;
  /* 기본적으로 숨김 */
  transition: opacity 0.35s ease-in-out, transform 0.35s ease-in-out;
  transform: translateY(-10px);
}

/* hover 시 텍스트 등장 */
div.box-container figure:hover h3,
div.box-container figure:hover p {
  opacity: 1;
  transform: translateY(0);
  transition-delay: 0.15s;
}



