/* * 기본적으로 안 보이게 설정 */
.fade-section {
  opacity: 0;
  transform: translateY(30px);
  /* 기본적으로 약간 아래에서 시작 */
  transition: opacity 1s ease, transform 2s ease;
}

/* 화면에 보이면 나타나는 효과 */
.fade-section.active {
  opacity: 1;
  transform: translateY(0);
}

/* ★ 특정 요소별로 다른 애니메이션 추가 */

/* 왼쪽에서 등장 */
.fade-section.left {
  transform: translateX(-50px);
}

.fade-section.left.active {
  transform: translateX(0);
}

/* 오른쪽에서 등장 */
.fade-section.right {
  transform: translateX(50px);
}

.fade-section.right.active {
  transform: translateX(0);
}

/* 페이드 인 */
.fade-section.fade {
  opacity: 0;
}

.fade-section.fade.active {
  opacity: 1;
}

/* 위에서 등장 */
.fade-section.top {
  transform: translateY(-50px);
}

.fade-section.top.active {
  transform: translateY(0);
}

/* 아래에서 등장 */
.fade-section.bottom {
  transform: translateY(50px);
}

.fade-section.bottom.active {
  transform: translateY(0);
}

/* 살짝 확대되면서 등장 */
.fade-section.zoom {
  transform: scale(0.8);
}

.fade-section.zoom.active {
  transform: scale(1);
}

