/* 상품 상세설명 박스 */

.description-box {
  /* border: 3px solid red; */
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 1);
  color: #000;
  font-size: 16px;
  text-align: center;
  opacity: 0;
  transform: translateY(100%);
  /* hover 전에는 아래쪽 */
  transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
  display: block;
  padding-top: 18vh;
  border-radius: 10px;
  /* padding:20vh 합으로 놥둘것 잘어울림 
  18 + 2 =20인 상태
  */
}

